import React from "react";
import styled from "styled-components";
import { Section } from "./FlexGrid";
import theme from "../styles/colors";
import { media } from "../styles/style-utils";
import { supportsMultilineTextGradient } from "../utils/supportsMultilineTextGradient.js";

const Headline = styled.h1`
  font-size: 1.5rem;
  font-weight: 400;
  ${media.medium`
    font-size: 2rem;
    `};
`;

const Tagline = Headline.withComponent("h2");

const FeatureSpan = styled.span(({ useFallbackGradientForMultiline }) => {
  const gradientTextFill = `@supports (-webkit-text-fill-color: transparent) {
      font-weight: 700;
      background: ${theme.featureGradientTilted};
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
    }`;

  const gradientBackgroundFallback = `
  @supports (background: ${theme.featureGradientTilted}) {
    background: ${theme.featureGradientTilted};
    color: ${theme.foreground};
    padding-left: 6px;
    padding-right: 6px;
  }
`;

  return `
    ${
      useFallbackGradientForMultiline
        ? gradientBackgroundFallback
        : gradientTextFill
    }
  `;
});

const HeroText = styled.div`
  max-width: 900px;
  vertical-align: middle;
`;

const Hero = () => (
  <Section>
    <HeroText>
      <Headline accessibilityRole="heading" aria-level="2">
        Hi, I&#39;m Will Griffiths.
      </Headline>
      <Tagline accessibilityRole="heading" aria-level="2">
        I&#39;m a{" "}
        <FeatureSpan
          useFallbackGradientForMultiline={!supportsMultilineTextGradient}
        >
          front-end developer
        </FeatureSpan>{" "}
        from Brisbane, Australia.
      </Tagline>
    </HeroText>
  </Section>
);

export default Hero;
